import React from 'react'
import Helmet from 'react-helmet'
import Me from '../../../../static/me.jpg'
import config from '../../../../data/Config'

const Head = ({ children, type, headline, datePublished, dateModified, cover, location = '' }) => {
	const structuredDataArticle = `{
		"@context": "http://schema.org",
		"@type": "${type}",
		"mainEntityOfPage": {
			"@type": "WebPage",
			"@id": "https://google.com/article"
		},
		"headline": "${headline}",
		"image": {
			"@type": "imageObject",
			"url": "${cover ? `https://nostalgic-morse-7a6322.netlify.com${cover}` : `https://nostalgic-morse-7a6322.netlify.com${Me}`}",
			"height": "600",
			"width": "800"
		},
		"datePublished": "${datePublished}",
		"dateModified": "${dateModified}",
		"author": {
			"@type": "Person",
			"name": "Ismail Ghallou (Smakosh)"
		},
		"publisher": {
		"@type": "Organization",
		"name": "Ismail Ghallou (Smakosh)",
		"logo": {
			"@type": "ImageObject",
			"url": "https://nostalgic-morse-7a6322.netlify.com/favicon/logo-48.png"
		}
		},
		"description": "${headline}"
	}`

	const structuredDataOrganization = `{ 
    "@context" : "http://schema.org",
    "@type" : "${type}",
    "legalName" : "Ismail Ghallou",
    "url" : "https://nostalgic-morse-7a6322.netlify.com/",
    "logo" : "https://nostalgic-morse-7a6322.netlify.com/favicon/logo-48.png",
    "contactPoint" : [{
		"@type" : "ContactPoint",
		"telephone" : "",
		"contactType" : "customer service"
    }],
    "sameAs" : [ 
		"http://www.facebook.com/",
		"http://www.twitter.com/",
		"https://www.youtube.com/user/",
		"https://www.linkedin.com/in/"
	]
  }`

	return (
		<Helmet>
			<meta name="description" content={type === 'NewsArticle' ? headline : config.description} />
			<meta name="image" content={cover ? `https://nostalgic-morse-7a6322.netlify.com${cover}` : `https://nostalgic-morse-7a6322.netlify.com${Me}`} />

			<meta property="og:url" content={`${config.url}${location}/?ref=smakosh.com`} />
			<meta property="og:type" content={type === 'NewsArticle' ? 'NewsArticle' : 'website'} />
			<meta property="og:title" content={type === 'NewsArticle' ? headline : config.copyright.label} />
			<meta property="og:description" content={type === 'NewsArticle' ? headline : config.description} />
			<meta property="og:image" content={cover ? `https://nostalgic-morse-7a6322.netlify.com${cover}` : `https://nostalgic-morse-7a6322.netlify.com${Me}`} />
			<meta property="fb:app_id" content={config.social.facebook} />

			<meta name="twitter:card" content={cover ? `https://nostalgic-morse-7a6322.netlify.com${cover}` : `https://nostalgic-morse-7a6322.netlify.com${Me}`} />
			<meta name="twitter:creator" content={config.social.twitter} />
			<meta name="twitter:site" content="@smakosh" />
			<meta name="twitter:title" content={type === 'NewsArticle' ? headline : config.copyright.label} />
			<meta name="twitter:description" content={type === 'NewsArticle' ? headline : config.description} />
			<meta name="twitter:image:src" content={cover ? `https://nostalgic-morse-7a6322.netlify.com${cover}` : `https://nostalgic-morse-7a6322.netlify.com${Me}`} />
			<script type="application/ld+json">{type === 'NewsArticle' ? structuredDataArticle : structuredDataOrganization}</script>
			<title>{ children }</title>
		</Helmet>
	)
}

export { Head }



